"use client";

import { gql } from "@apollo/client";
import { client } from "@/lib/apollo-client";
import { useEffect, useState } from "react";

// Define types for your data
interface Game {
  id: string;
  title: string;
  platform: string;
}

interface GetGamesData {
  games: Game[];
}

// GraphQL query to get all games
const GET_GAMES = gql`
  query {
    games {
      id
      title
      platform
    }
  }
`;

export default function GamesPage() {
  const [games, setGames] = useState<Game[]>([]);
  const [search, setSearch] = useState(""); // <-- NEW
  const [platformFilter, setPlatformFilter] = useState("All"); // <-- NEW
  const [loading, setLoading] = useState(true);

  // Grab data on first load
  useEffect(() => {
    async function fetchGames() {
      try {
        const { data } = await client.query<GetGamesData>({
          query: GET_GAMES,
        });

        if (data && data.games) {
          setGames(data.games);
        }
      } catch (error) {
        console.error("Error fetching games:", error);
      } finally {
        setLoading(false);
      }
    }

    fetchGames();
  }, []);

  // FILTERING LOGIC (search + platform)
  const filteredGames = games
    .filter((game) =>
      platformFilter === "All" ? true : game.platform === platformFilter
    )
    .filter((game) =>
      game.title.toLowerCase().includes(search.toLowerCase())
    );

  // Loading state
  if (loading) {
    return (
      <main>
        <h1>Game List</h1>
        <p>Loading...</p>
      </main>
    );
  }

  return (
    <main style={{ padding: "20px" }}>
      <h1>Game List</h1>

      {/* Search Bar */}
      <input
        type="text"
        placeholder="Search games..."
        value={search}
        onChange={(e) => setSearch(e.target.value)}
        style={{
          padding: "10px",
          margin: "10px 0",
          width: "100%",
          maxWidth: "400px",
          display: "block",
        }}
      />

      {/* Platform Dropdown Filter */}
      <select
        value={platformFilter}
        onChange={(e) => setPlatformFilter(e.target.value)}
        style={{
          padding: "10px",
          margin: "10px 0",
          width: "100%",
          maxWidth: "400px",
        }}
      >
        <option value="All">All Platforms</option>
        <option value="Nintendo Switch">Nintendo Switch</option>
      </select>

      {/* No matching results */}
      {filteredGames.length === 0 ? (
        <p>No games match your search.</p>
      ) : (
        <ul>
          {filteredGames.map((game) => (
            <li key={game.id}>
              <a href={`/games/${game.id}`}>
                <strong>{game.title}</strong> â€” {game.platform}
              </a>
            </li>
          ))}
        </ul>
      )}
    </main>
  );
}
